<template>
	<div>
		<div id="localtracks" style="width: 1280px;"></div>
		<button @click="joinRoom">加入房间</button>
	</div>
</template>

<script>
	import axios from 'axios'
	import * as QNRTC from 'pili-rtc-web'
	import LiveModel from '../model/live.js'
	export default {
		data() {
			return {
				live: null,
				roomToken: '',
				myRTC: null
			};
		},
		created() {
			QNRTC.log.setLevel("disable")
			this.live = new LiveModel()
			this.myRTC = new QNRTC.TrackModeSession();
		},
		methods: {
			async joinRoom() {
				try {
					const res = await this.live.getRoomToken('123', 'admin')
					this.roomToken = res.data.room_token
					await this.myRTC.joinRoomWithToken(this.roomToken);
					
					const users =  await this.publish(this.myRTC);
					
					/* this.showBtn = false */
					// 订阅房间用户发布的事件，并且由房主发出合流信令
					/* this.localVideo() */
				} catch (error) {
					console.log('加入房间失败', error)
				}
			},

			// 增加一个函数 publish，用于采集并发布自己的媒体流
			// 这里的参数 myRoom 是指刚刚加入房间时初始化的 Session 对象
			async publish(myRoom) {
				// 我们打开了 3 个参数，即采集音频，采集视频，采集屏幕共享。
				// 这个函数会返回一个列表，列表中每一项就是一个音视频轨对象
				const localTracks = await QNRTC.deviceManager.getLocalTracks({
					audio: {
						enabled: true,
						tag: "audio"
					},
					/* video: {
						enabled: true,
						tag: "video"
					}, */
					screen: {
						enabled: true,
						tag: "screen",
						width: 1280,
						height: 720,
						bitrate: 1800
					},
				});
				
				console.log(localTracks)
				
				const localElement = document.getElementById("localtracks");
				for (const localTrack of localTracks) {
					// 如果这是麦克风采集的音频 Track，我们就不播放它。
					if (localTrack.info.tag === "audio") continue;
					// 调用 Track 对象的 play 方法在这个元素下播放视频轨
					localTrack.play(localElement, true);
					
				}
				
				
				// 将刚刚的 Track 列表发布到房间中
				/* await myRoom.publish(localTracks); */
				this.mergeTracks(localTracks, myRoom)
				console.log("publish success!");
			},
			
			mergeTracks(localTracks, myRoom) {
				let options = []
				localTracks.forEach( (item) => {
					
					if (item.info.tag === 'video') {
						let vOpt = {
							trackId: item.info.trackId,
							x: 20,
							y: 0,
							w: 200,
							h: 200
						}
						options.push(vOpt)
					} else if (item.info.tag === 'screen') {
						let sOpt = {
							trackId: item.info.trackId,
							x: 0,
							y: 0,
							w: 1280,
							h: 720
						}
						options.push(sOpt)
					} else {
						let aOpt = {
							trackId: item.info.trackId
						}
						options.push(aOpt)
					}
				})
				console.log(options)
				myRoom.addMergeStreamTracks(options);
			}
			
			
		},
	}
</script>

