<template>
	<div class="main">
		<button @click="joinRoom" class="btn" v-if="showBtn">打开摄像头</button>
		<div id="localplayer" class="self"></div>
		
		<div class="pdf" v-if="showPPT">
			<iframe :src="ppt_url"  frameborder="0" width="100%" height="100%"></iframe>
		</div>
		
		<div class="paint">
			<canvas class="myCanvas" @mousedown="write($event)" ref="myCanvas"></canvas>

		</div>
		<!-- <button @click="showCanvas" class="btn btn-paint" >打开画板</button> -->
		<div class="draw">
			<div class="pen common icon-pen iconfont" @click="showPen=!showPen" ref="pen">
				<div class="color-box" v-show="showPen">
					<div class="color" v-for="item in colorAll" :style="`backgroundColor:${item}`" @click="showCanvas(item)"></div>
				</div>
			</div>
			<div class="eraser common icon-xiangpica iconfont" @click="useEraser" ref="eraser"></div>
			<div class="common iconfont icon-qingchuhuancun clear" @click="cancelDraw"></div>
		</div>
		
		<div class="handle" @click="openHandle">
			<span class="iconfont icon-custom-teacher"></span>
		</div>
		
		<div class="ppt-box" v-if="!showPPT">
			<div class="ppt-info" v-for="item in pptList" @dblclick="openPPT(item)">
				<div class="ppt-img"><img src="../../public/PPT.svg" alt=""></div>
				<span class="ppt-name">{{item}}</span>
			</div>
			
			
		</div>
		
	</div>
</template>

<script>
	import axios from 'axios'
	import * as QNRTC from 'pili-rtc-web'
	import LiveModel from '../model/live.js'
	export default {

		data() {
			return {
				showPPT: false,
				show: false,
				roomId: '',
				appId: '',
				userId: '',
				roomToken: '',
				myRTC: null,
				MasterId: '11111', //房主id
				publishedInfo: [], //已经推流的用户 ， 用来记录画布配置信息， 掉线重连后房主根据这个信息推流。
				live: null,
				showBtn: true,
				ctx: null,
				colorAll: [
					'#000000',
					'#d81e06',
					'#0622d8',
					'#d806a5',
					'#1296db',
					'#f4ea2a'
				],
				showPen: false,
				nowItem: '',
				eraser: false,
				src: "",
				pptList: [],
				ppt_url:''
			}
		},
		created() {
		/* 
			let uri = window.document.location.origin + '/#/' + '/handle'
			window.open(uri, "",  'height=400, width=400, top=200, left=40, toolbar=no,titlebar=no,menubar=no, scrollbars=no, resizable=no, location=no, status=no'); */
			/* this.getPublishedInfo() */
			console.log(1)
			this.live = new LiveModel()
			this.getPPT()

		},
		mounted() {
			
			this.$refs.myCanvas.width = document.body.clientWidth;
			this.$refs.myCanvas.height = document.body.clientHeight - 25;
		},

		


		methods: {
			openHandle() {
				const uri = `${window.document.location.origin}/#/handle`
				window.open(uri, "",  'height=400, width=400, top=200, left=40, toolbar=no,titlebar=no,menubar=no, scrollbars=no, resizable=no, location=no, status=no'); 
				
			},
			
			openPPT(item) {
				this.showPPT = true
				this.ppt_url = `https://view.officeapps.live.com/op/embed.aspx?src=https://www.cxzweb.club/api/ppt/${item}`
				console.log(`https://www.cxzweb.club/api/ppt/${item}`)
				setTimeout(()=>{
					this.$refs.myCanvas.width = document.body.clientWidth;
					this.$refs.myCanvas.height = document.body.clientHeight - 25;
				},2000)
				
			},
			getPPT() {
				axios.get('https://www.cxzweb.club/api/getPPT').then((res)=>{
					this.pptList = res.data
					console.log(this.pptList)
				})
			},
			
			cancelDraw() {
				let ctx = this.$refs.myCanvas.getContext("2d")
				ctx.clearRect(0, 0, document.body.clientWidth, document.body.clientHeight)
			},
			useEraser() {
				this.eraser = true
				this.$refs.pen.style = 'color:black;background-color:white'
				this.$refs.eraser.classList.add('eraser-active')

			},

			showCanvas(item) {
				this.nowColor = item
				this.eraser = false
				this.$refs.eraser.classList.remove('eraser-active')
				this.$refs.pen.style = `background-color:${item};color:white`
			},

			write(e) {
				let src = this.src
				let canvas = this.$refs.myCanvas
				let ctx = this.$refs.myCanvas.getContext("2d")

				if (this.eraser) {
					this.$refs.myCanvas.onmousemove = function(e) {
						ctx.clearRect(e.pageX - 5, e.pageY - 5, 15, 15)
					}
					this.$refs.myCanvas.onmouseup = (e) => {
						this.$refs.myCanvas.onmousemove = null;
					}

					return
				}

				ctx.beginPath()
				ctx.moveTo(e.pageX, e.pageY)
				ctx.lineWidth = '4'
				ctx.strokeStyle = this.nowColor
				this.$refs.myCanvas.onmousemove = function(e) {
					ctx.lineTo(e.pageX, e.pageY)
					ctx.stroke()
					/* src=canvas.toDataURL("image/png") ; */
					console.log(src)
				}
				this.$refs.myCanvas.onmouseup = (e) => {
					this.$refs.myCanvas.onmousemove = null;
				}

			},
			//新接口文档 https://gitlab.dreamdev.cn/live/live-class-room/blob/rtn/docs/rtn.md

			async joinRoom() {
				this.showBtn = false
				this.localVideo()
			},

			subscribeUser(myRTC, user) {
				// 如果用户没有发布就直接返回
				if (!user.published) {
					return
				}
				myRTC.subscribe(user.userId).then(remoteStream => {
					// 我们在 room 页面上准备用来显示远端媒体流的元素
					const remotePlayer = document.getElementById('remoteplayer')
					remoteStream.play(remotePlayer)
					/* this.removeStyle() */

				}).catch(e => {
					console.log('subscribe error!', e)
				})

			},



			/* removeStyle() {
				let parent = this.$refs.remoteplayer
				let liveObj = parent.getElementsByTagName('video')
				let liveList = Array.prototype.slice.call(liveObj)
				console.log(liveList, "清除原本样式")
				liveList.forEach((item) => {
					item.style = ''
				})
			}, */

			/**
			 * 监听各种事件，上传埋点
			 */

			/* _addlisten(myRTC) {
				if (this.userId === this.MasterId) {
					myRTC.on('user-join', (user) => {
						console.log('有用户进来！')
						this.live.uploadRecord(this.roomId, user.userId, 'joined')
					})

					myRTC.on('user-leave', (user) => {
						console.log('有用户进来！')
						this.live.uploadRecord(this.roomId, user.userId, 'leaved')
					})

					myRTC.on('user-unpublish', (user) => {
						console.log('有用户进来！')
						this.live.uploadRecord(this.roomId, user.userId, 'unpublished')
					})

					myRTC.on('mute', (e) => {
						this.live.uploadRecord(this.roomId, e.userId, 'mute')
					})
				}
			}, */

			firstSubscribe(users) {
				for (let i = 0; i < users.length; i += 1) {
					const user = users[i]
					// 如果当前房间的用户不是自己并且已经发布，那就订阅他
					if (user.published && user.userId != this.myRTC.userId) {
						this.subscribeUser(this.myRTC, user)
					}
				}
			},

			async localVideo() {
				const localStream = await QNRTC.deviceManager.getLocalStream({
					video: {
						enabled: true,
						width: 1280,
						height: 720,
						bitrate: 1500
					},
					audio: {
						enabled: true,
						bitrate: 32
					}
				})
				// 发布刚刚采集到的媒体流到房间

				const localVideo = document.getElementById('localplayer')
				// 调用媒体流的 play 方法，在我们指定的元素上播放媒体流，其中第二个参数代表 静音播放
				localStream.play(localVideo, true)
				/* this.removeStyle() */
			},

			/**
			 *踢掉或者邀请用户 
			 *
			 */
			changeUser(action) {
				this.live.changeUser(this.roomId, this.userId, action).then((res) => {
					console.log(res)
				})
			},

			getOptions(userId) {
				let user = this.publishedInfo.find((item) => {
					return item.userId == userId
				})
				if (user) {
					return user.videoInfo
				} else {
					let newPublishedInfo = {
						userId,
						videoInfo: {
							x: 200 * this.publishedInfo.length,
							y: 0,
							w: 200,
							h: 200,
							z: 1,
							hidden: false,
							muted: false
						}
					}
					this.publishedInfo.push(newPublishedInfo)
					this.setPublishedInfo(this.publishedInfo)
					return newPublishedInfo.videoInfo
				}
			},

			setPublishedInfo(newInfo) {
				let infoString = JSON.stringify(newInfo)
				sessionStorage.setItem("publishedInfo", infoString)
			},

			getPublishedInfo() {
				let string = sessionStorage.getItem("publishedInfo")
				if (string) {
					this.publishedInfo = JSON.parse(string)
				} else {
					this.publishedInfo = []
				}
			}
		}
	}
</script>

<style scoped>
	.main {
		background-color: rgba(21, 21, 21);
		width: 100%;
		height: 100%;
		position: relative;
		overflow: hidden;
	}

	.btn {
		position: absolute;
		left: 50%;
		top: 14px;
		transform: translateX(-50%);
		width: 450px;
		height: 50px;
		border-radius: 20px;
		border: none;
		background-color: chocolate;
		color: white;
		cursor: pointer;
		font-size: 22px;
		letter-spacing: 2px;
		z-index: 110;
		outline: none;

	}

	.btn-paint {
		left: 80%;
		top: 90%;
		width: 120px;
		height: 30px;
		font-size: 16px;
		z-index: 80;
	}

	.btn:active {
		background-color: orangered;
	}

	.self {
		width: 400px;
		height: 300px;

		position: absolute;
		z-index: 30;
		left: 0px;
		top: 30px;
	}

	.self .qnrtc-stream-player {
		width: 100%;
		height: 100%;
	}

	.par {
		width: 100%;
		height: 100%;
		overflow: hidden;
	}

	.child {
		width: 100%;
		height: 100%;
		margin-left: -289px;
		margin-top: -147px;
	}


	.pdf {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0px;
		top: 0px;
		z-index: 22;
	}

	.if {
		object-fit: fill;
	}

	canvas {
		
		position: absolute;
		z-index: 50;
	}

	.paint {
		width: 100%;
		height: calc(100% - 25px);
		z-index: 50;
		position: absolute;
		left: 0px;
		top: 0px;
		
	}

	.draw {
		position: absolute;
		z-index: 200;
		right: 50px;
		bottom: 5px;
		display: flex;
		min-width: 300px;
	}
	
	.handle{
		position: absolute;
		z-index: 200;
		left: 50px;
		bottom: 3px;
		background: white;
		padding: 1px;
		border-radius:50px;
		padding-left: 5px;
		padding-right: 5px;
		cursor: pointer;
	}
	
	.handle:active{
		color: deeppink;
	}





	.color {
		width: 30px;
		height: 30px;
		margin-bottom: 4px;
		cursor: pointer;
		box-sizing: border-box;
		transition: transform 0.1s linear;

	}

	.color:active {
		transform: scale(0.7);
	}

	.draw .eraser {

		font-size: 16px;
	}

	.draw .pen {
		font-size: 13px;
		background: black;
		color: white;
		box-sizing: border-box;
	}

	.pen:active {
		padding-top: 1px;

	}

	.pen .color-box {
		position: absolute;
		width: 70px;
		background: lightgray;
		left: -50px;
		top: -110px;
		animation: out ease-in 0.2s forwards;
		transform-origin: right bottom; 
		display: flex;
		justify-content: space-around;
		flex-wrap: wrap;
		flex-direction: row;
		padding-top: 4px;

	}
	
	@keyframes out{
		0%{
			transform: scale(0);
		}
		80%{
			transform: scale(1.2);
		}
		100%{
			transform: scale(1);
		}
	}



	.common {
		margin-left: 5px;
		font-size: 18px;
		height: 20px;
		width: 20px;
		line-height: 20px;
		text-align: center;
		color: black;
		background: white;
		border-radius: 10px;
		cursor: pointer;
		transition: transform 0.1s linear;
	}

	.clear:active {
		background: darkorange;
		color: white;
	}

	.eraser-active {
		background: hotpink;
		color: black;
	}

	.eraser:active {
		transform: scale(0.8);
	}
	
	.ppt-box {
		position: absolute;
		z-index: 999;
		background-color: white;
		width: 1000px;
		height: 500px;
		left: 50%;
		top: 20%;
		transform: translateX(-50%);
		border-radius: 20px;
		padding: 10px;
		box-sizing: border-box;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		overflow-y: scroll;
	}
	
	.ppt-box::-webkit-scrollbar{
		display: none;
	}
	
	.ppt-info{
		width: 100px;
		height: 105px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin-right: 15px;
		margin-bottom: 10px;	
	}
	
	.ppt-info:active{
		background-color: #FF4500;
		color: white;
	}
	
	.ppt-img{
		width: 100px;
		height: 80px;
		margin-bottom: 5px;
	}
	.ppt-img img{
		width: 100%;
		height: 100%;
	}
</style>
