import Vue from 'vue'
import Router from 'vue-router'
import LoginRoom from './components/LoginRoom.vue'
import showVideo from './components/ShowVideo.vue'
import screenVideo from './components/ScreenVideo.vue'
import readPDF from './components/ReadPDF.vue'
import UploadPPT from  './components/UploadPPT.vue'
import Subscribe from './components/Subscribe.vue'
import Publish from './components/Publish.vue'
import TeacherHandle from './components/TeacherHandle.vue'


/* import StudentLive from './components/StudentLive.vue' */

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			component: LoginRoom
		},
		{
			path: '/live',
			component: showVideo
		},
		{
			path: '/screen',
			component: screenVideo
		},
		{
			path: '/pdf',
			component: readPDF
		},
		{
			path: '/upload',
			component: UploadPPT
		},
		{
			path: '/pub',
			component: Publish
		},
		{
			path: '/sub',
			component: Subscribe
		},
		{
			path: '/handle',
			component: TeacherHandle
		}
	]
})
