<template>
	<div>
		<form @submit.prevent="toVideo">
			<div>
				<label for="">房间号</label>
				<input v-model="roomToken" type="text" required="required">
			</div>
			<div>
				<label for="">用户名</label>
				<input v-model="userName" type="text" required="required">
			</div>
			<button type="submit">加入房间</button>
		</form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				roomToken: '',
				userName: ''
			};
		},
		methods: {
			toVideo() {
				this.$router.push({
					path: '/screen',
					query: {
						roomId: this.roomToken,
						userId: this.userName
					}
				})
			}
		}
	}
</script>

