<template>
	<div class="uploadPPT">
		<!-- http://111.231.232.210:7001 -->
		
			<div class="file">
				<label class="box">
					<input name="file" type="file" class="ppt-input" style="display: none;" ref="myForm" @change="uploadPPT"/>
					<div type="submit" class="btn" @submit="submit">上传到服务器</div>
				</label>
			</div>
			<x-loading v-if="show"></x-loading>
		
	</div>
</template>

<script>
	import {uploadAvatar} from '../model/upload.js'
	import XLoading from '../basecomponents/x-loading/XLoading.vue'
	export default {
		data() {
			return {
				control: false,
				show: false
			}
		},
		created() {
			
			
		},
		components: {
			XLoading 
		},
		methods: {
			ajax() {
				if (this.$refs.myForm.files[0]) {
					this.control = true
				}
			},
			
			submit() {
				if(!this.control) {
					return false
				}
			},
			
			uploadPPT() {
				const file = this.$refs.myForm.files[0]
				if(!file) {
					return
				}
				this.$bac.add()
				this.show = true
				uploadAvatar(file).then((res)=>{
					
					this.$bac.remove()
					this.show= false
				})
			}
			
		}
	}
</script>

<style scoped>
	.uploadPPT{
		width: 100%;
		height: 100%;
		background-color: rgb(21, 21, 21);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
	}
	
	.ppt{
		color: white;
		margin: 0;
		padding: 0;
	}
	
	.file{
		box-sizing: border-box;
		
		
		
	}
	
	/* .box{
		width: 400px;
		height: 44px;
		background-color: rgb(52, 170, 220);
		display: block;
	} */
	
	.btn{
		width: 400px;
		height: 44px;
		border: none;
		background: none;
		color: white;
		background-color: hotpink;
		border-radius: 20px;
		font-size: 22px;
		line-height: 44px;
		letter-spacing: 10px;
		cursor: pointer;
		text-align: center;
	}
</style>
