<template>
	<div class="main">
		<button @click="joinRoom">加入房间</button>
		
		<div id="localplayer" class="my-live">
			
			<div id="remoteplayer" class="user-live" ref="remoteplayer">
				
			</div>
		</div>
		
	</div>
</template>

<script >
	import axios from 'axios'
	import * as QNRTC from 'pili-rtc-web'
	import LiveModel from '../model/live.js'
	export default {
		
		data() {
			return {
				roomId: '',
				appId: '',
				userId: '',
				roomToken: '',
				myRTC: null,
				MasterId:'11111', //房主id
				publishedInfo: [], //已经推流的用户 ， 用来记录画布配置信息， 掉线重连后房主根据这个信息推流。
				live:null
			}
		},
		created() {
			this.getPublishedInfo()
			this.userId = this.$route.query.userId
			this.roomId = this.$route.query.roomId
			this.live = new LiveModel()
		},
		mounted() {
			QNRTC.log.setLevel("disable")// log.setLevel("disable")  关掉日志disable warning debug log 四个选项可选
			this.myRTC = new QNRTC.QNRTCSession() // 创建QNRTCSession实例，监听事件,订阅流等
			window.onbeforeunload = () => {
                this.myRTC.leaveRoom();
            };
		},

		destroyed() {
			this.myRTC.leaveRoom() // 离开房间
		},


		methods: {
		//新接口文档 https://gitlab.dreamdev.cn/live/live-class-room/blob/rtn/docs/rtn.md
			
			async joinRoom() {
				try {
					const res = await this.live.getRoomToken(this.roomId, this.userId)
					this.roomToken = res.data.room_token
					const users = await this.myRTC.joinRoomWithToken(this.roomToken) // 加入房间，获取当且房间的用户数量
					console.log('加入房间成功! 当前房间的用户:', users)

					// 订阅房间用户发布的事件，并且由房主发出合流信令
					
					this._addlisten(this.myRTC) //监听事件
					
					this.myRTC.on('user-publish', user => {
						// 让房主发出新进用户的合流信令
						this.mergeOther(user.userId)
						//订阅新推流用户的的画面到本地
						this.subscribeUser(this.myRTC, user)
						this.live.uploadRecord(this.roomId, this.userId, 'published').then((res)=>{
							console.log(res)
						}) //埋点
					})
					
					
					
					
					this.myRTC.on('republish', () => {
						console.log('重新发布')
						// 房主掉线 需要重新提交自己的合流信令。
						this.mergeOwner()
					})

					this.myRTC.on('resubscribe', (userId) => {
						console.log('重新订阅别人', userId)
						this.removeStyle()
						setTimeout(()=>{
							this.removeStyle()
						}, 500) 
					})

					this.myRTC.on('error', error => {
						console.log('发生错误', error)
					})



					//进房订阅所有已经发布视频的成员
					this.firstSubscribe(users)

					// 采集本地媒体流, 并且发布
					this.localVideo()
				} catch (error) {
					console.log('加入房间失败', error)
				}

			},

			subscribeUser(myRTC, user) {
				// 如果用户没有发布就直接返回
				if (!user.published) {
					return
				}
				myRTC.subscribe(user.userId).then(remoteStream => {
					// 我们在 room 页面上准备用来显示远端媒体流的元素
					const remotePlayer = document.getElementById('remoteplayer')
					remoteStream.play(remotePlayer)
					this.removeStyle()

				}).catch(e => {
					console.log('subscribe error!', e)
				})

			},
			
			/**
			 * 房主把自己的流提交给七牛合并
			 */
			mergeOwner() {
				if(this.userId == this.MasterId) {
					console.log('房主合流成功')
					this.myRTC.setMergeStreamLayout(this.MasterId, {
						x: 0,
						y: 0,
						w: 1280,
						h: 720,
						hidden: false,
						muted: false,
					})
				}
			},
			
			mergeOther(userId) {
				// 只让房主控制合流的信息的。
				if(this.userId == this.MasterId) {
					let options = {
						x: Math.ceil(Math.random() * 9) * 100,
						y: 0,
						w: 200,
						h: 200,
						z: 1,
						hidden: false,
						muted: false,
					}
					console.log('新用户的流合并成功，画布配置为：', options)
					this.myRTC.setMergeStreamLayout(userId, options)
				}
			},
			
			removeStyle() {
				let parent = this.$refs.remoteplayer
				let liveObj = parent.getElementsByTagName('video')
				let liveList = Array.prototype.slice.call(liveObj)
				console.log(liveList, "清除原本样式")
				liveList.forEach((item)=>{
					item.style = ''
				})
			},
			
			/**
			 * 监听各种事件，上传埋点
			 */
		
			_addlisten(myRTC) {
				if(this.userId === this.MasterId) {
					myRTC.on('user-join', (user) => {
						console.log('有用户进来！')
						this.live.uploadRecord(this.roomId, user.userId, 'joined')
					})
					
					myRTC.on('user-leave', (user) => {
						console.log('有用户进来！')
						this.live.uploadRecord(this.roomId, user.userId, 'leaved')
					})
					
					myRTC.on('user-unpublish', (user) => {
						console.log('有用户进来！')
						this.live.uploadRecord(this.roomId, user.userId, 'unpublished')
					})
					
					myRTC.on('mute', (e) => {
						this.live.uploadRecord(this.roomId, e.userId, 'mute')
					})
				}
			},
			
			firstSubscribe(users) {
				for (let i = 0; i < users.length; i += 1) {
					const user = users[i]
					// 如果当前房间的用户不是自己并且已经发布，那就订阅他
					if (user.published && user.userId != this.myRTC.userId) {
						this.subscribeUser(this.myRTC, user)
					}
				}
			},
			
			async localVideo() {
				const localStream = await QNRTC.deviceManager.getLocalStream({
					video: {
						enabled: true,
						width: 640,
						height: 480,
						bitrate: 600
					},
					audio: {
						enabled: true,
						bitrate: 32
					}
				})
				// 发布刚刚采集到的媒体流到房间
				await this.myRTC.publish(localStream)
				const localVideo = document.getElementById('localplayer')
				// 调用媒体流的 play 方法，在我们指定的元素上播放媒体流，其中第二个参数代表 静音播放
				localStream.play(localVideo, true)
				this.removeStyle()
				// 把房主的流提交给七牛合并
				this.mergeOwner()
			},
			
			/**
			 *踢掉或者邀请用户 
			 *
			 */
			changeUser(action) {
				this.live.changeUser(this.roomId, this.userId, action).then((res) => {
					console.log(res)
				})
			},
			
			getOptions(userId) {
				let user = this.publishedInfo.find((item)=>{
					return item.userId == userId
				})
				if(user) {
					return user.videoInfo
				} else {
					let newPublishedInfo = {
						userId,
						videoInfo: {
							x: 200*this.publishedInfo.length,
							y: 0,
							w: 200,
							h: 200,
							z: 1,
							hidden: false,
							muted: false
						}
					}
					this.publishedInfo.push(newPublishedInfo)
					this.setPublishedInfo(this.publishedInfo)
					return newPublishedInfo.videoInfo
				}
			},
			
			setPublishedInfo(newInfo) {
				let infoString = JSON.stringify(newInfo)
				sessionStorage.setItem("publishedInfo", infoString)
			},
			
			getPublishedInfo() {
				let string = sessionStorage.getItem("publishedInfo")
				if(string) {
					this.publishedInfo = JSON.parse(string)
				} else {
					this.publishedInfo = []
				}
			}			
		}
	}
</script>

<style scoped>
	
	.main {
		background-color: rgba(21, 21, 21);
		width: 100%;
		height: 100%;
		position: relative;
		
	}
	.my-live {
		width: 500px;
		height: 375px;
		background-color: white;
		position: absolute;
		bottom: 50%;
		top:20%;
	}
	
	.my-live .qnrtc-stream-player{
		width: 100%;
		height: 100%;
	}
	
	.user-live {
		height: 90px;
		position: absolute;
		bottom: 0;
		display: flex;
		flex-direction: row;
		width: 100%;
		justify-content: flex-end;
	}
	
	.user-live .qnrtc-stream-player{
		display: block; 
		width: 120px;
		height: 90px;
		background: rgba(0, 0, 0, 0.1);
		margin-left: 1px;
	}
	
	.kick {
		position: absolute;
		width: 200px;
		height: 200px;
		font-size: 30px;
		line-height: 22px;
		z-index: 100000;
		background: red;
	}

</style>
